<template>
    <div class="chat-container">
      <div class="chat-box">
        <div v-for="(message, index) in messages" :key="index" class="message" :class="message.role">
          <div class="message-content" v-html="formatMessage(message.content)"></div>
        </div>
      </div>
  
      <div class="input-area">
        <input v-model="userMessage" @keyup.enter="sendMessage" placeholder="Escribe tu pregunta..." />
        <button @click="sendMessage">Enviar</button>
      </div>
    </div>
  </template>
  
  <script>
  import axios from "axios";
  
  export default {
    data() {
      return {
        userMessage: "",
        messages: []
      };
    },
    methods: {
      async sendMessage() {
        if (this.userMessage.trim() === "") return;
  
        // Agregar el mensaje del usuario al chat
        this.messages.push({ role: "user", content: this.userMessage });
  
        try {
          // Hacer la petición a la API de contabilidad
          const response = await axios.post("https://localhost:7248/api/Contabilidad/preguntar", {
            pregunta: this.userMessage
          }, {
            headers: {
              'Content-Type': 'application/json',
              'accept': '*/*'
            }
          });
  
          console.log(response.data);
          // Agregar la respuesta de la API al chat
          if (response.data && response.data.respuesta) {
            this.messages.push({ role: "system", content: response.data.respuesta });
          } else {
            this.messages.push({
              role: "system",
              content: "Lo siento, no recibí una respuesta válida de la API."
            });
          }
        } catch (error) {
          console.error("Error al obtener la respuesta:", error);
          this.messages.push({
            role: "system",
            content: "Lo siento, ocurrió un error al procesar tu pregunta."
          });
        }
  
        // Limpiar el campo de entrada
        this.userMessage = "";
      },
      formatMessage(message) {
        // Detectar si el mensaje contiene una tabla
        if (message.includes("|")) {
          return this.convertMarkdownTableToHTML(message);
        }
        return message;
      },
      convertMarkdownTableToHTML(markdown) {
        // Convertir el texto en formato Markdown a una tabla HTML
        const rows = markdown.trim().split("\n");
        const tableRows = rows.map(row => {
          const cells = row.split("|").map(cell => cell.trim()).filter(cell => cell !== "");
          return `<tr><td>${cells.join("</td><td>")}</td></tr>`;
        }).join("");
  
        // Crear el HTML de la tabla
        return `<table class="table">${tableRows}</table>`;
      }
    }
  };
  </script>
  
  <style scoped>
  .chat-container {
    width: 1000px;
    margin: 0 auto;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    padding: 20px;
    display: flex;
    flex-direction: column;
    height: 500px;
  }
  
  .chat-box {
    flex: 1;
    overflow-y: auto;
    margin-bottom: 20px;
  }
  
  .message {
    margin-bottom: 15px;
    padding: 10px;
    border-radius: 10px;
    max-width: 80%;
  }
  
  .user {
    background-color: #e1f5fe;
    align-self: flex-end;
  }
  
  .system {
    background-color: #e0e0e0;
    align-self: flex-start;
  }
  
  .message-content {
    word-wrap: break-word;
  }
  
  .input-area {
    display: flex;
    gap: 10px;
  }
  
  .input-area input {
    flex: 1;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .input-area button {
    padding: 10px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .input-area button:hover {
    background-color: #0056b3;
  }
  
  .table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 10px;
    text-align: left;
  }
  
  .table td, .table th {
    padding: 8px;
    border: 1px solid #ddd;
  }
  
  .table th {
    background-color: #f4f4f4;
  }
  </style>
  